li { 
      list-style: none;
      font-family: 'Open Sans', sans-serif;
      letter-spacing: -1px;
      font-size: 12.5px;
      font-weight: 800;
}

li:hover {
      font-size: 13px;
      animation: pulse; /* referring directly to the animation's @keyframe declaration */
      animation-duration: 0.3s; /* don't forget to set a duration! */
}

h1 {
      text-align: center;
      color: #111;
      font-family: 'Open Sans', sans-serif;
      margin-top: 0;
      padding-top: 10vh;
      font-size: 50px;
}

a {
      text-decoration: none;
      color: #222;
}

nav {
      width: 40%;
}

form{
      height: 20vh;
      padding: 10px;
}

label {
      font-size: 21px;
}
button{
      height: 30px;
      width: 400px;
}

.container{
      width: 100%;
      height: 100vh;
}

.hero-banner{
      background-color: #db346c;
      padding-top: 5vh;
      height: 15vh;
}

.header-nav{
      width: 100%;
      height: 100%;
      background-color: #fff;
}

.section-1, .section-2, .section-3, .section-default, .section-form{
      height: 80vh;
}

.card-style{
      color: #111;
      padding: 0px 25px;
      font-size: 21px;
      line-height: 45px;
}

.motivation-text {
      text-align: justify;
      overflow: auto;
      padding-right: 35px;
      width: 60%;
      height: 45vh;
      margin: auto;
      scrollbar-width: none;  /* Firefox */
}

.motivation-text::-webkit-scrollbar {
      display: none;
}

.coord-p{
      line-height: 12px;
      color: #111;
      padding: 0px 25px;
}

.coord-p a:hover{
      color: #db346c;
}

.button-container{
      width: 70%;
}

.div-button{
      margin-bottom: 10px;
      height: 70px;
      width: 150px;
      color: #111;
      padding: 20px;

}

.section-default p{
      margin-top: -15px;
}

.section-default a{
      font-size: 21px;
      text-align: center;
}

.section-default a:hover{
      color: #db346c;
      animation: pulse; /* referring directly to the animation's @keyframe declaration */
      animation-duration: 0.3s; /* don't forget to set a duration! */
}

.section-2 h2 {
      margin-top: 60px;
      margin-bottom: 0px;
      font-size: 40px;
}

.section-2 h3 {
      margin-top: 0px;
}

.section-2 h4 {
      font-size: 13px;
}

.section-2 p {
      text-align: justify;
}

.section-2 img {
      /*clip-path:ellipse(50% 50%);*/
      border: #111 solid 2px;
      border-radius: 50%;
}

.button-container{
      margin-top: 20px;
}

.text-input{
      height: 30px;
      width: 150%;
      border: solid 2px #111;
      border-radius: 20px;
      text-align: center;
      font-size: 15px;
}

.card{
      padding: 10px;
      color: #111;
      margin: 30px 0px;
}

.card-img {
      margin-right: 10px;
      width: 50px;
      height: 50px;
}

.card-text {
      width: 550px;
      border-bottom: 2px solid #111;
      line-height: 25px;
}

#logo {
      width: 130px;
}

#logo:hover {
      animation: pulse; /* referring directly to the animation's @keyframe declaration */
      animation-duration: 0.3s; /* don't forget to set a duration! */
}

/* --- Classes de Mise en page --- */

.flex-row-center-center {
      display: flex;
      flex-direction: row;
      justify-content: center;
      align-items: center;
}

.flex-row-space-around-center {
      display: flex;
      flex-direction: row;
      justify-content: space-around;
      align-items: center;
}

.flex-row-center-end {
      display: flex;
      flex-direction: row;
      justify-content: center;
      align-items: flex-end;
}

.flex-row-start-start{
      display: flex;
      flex-direction: row;
      justify-content: flex-start;
      align-items: flex-start;
}

.flex-row-start-end{
      display: flex;
      flex-direction: row;
      justify-content: flex-start;
      align-items: flex-end;
}

.flex-row-space-around-center {
      display: flex;
      flex-direction: row;
      justify-content: space-around;
      align-items: center;
}

.flex-col-center-center {
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
}

.flex-col-space-between-center {
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      align-items: center;
}

.flex-col-center-start {
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: flex-start;
}

.flex-col-start-center {
      display: flex;
      flex-direction: column;
      justify-content: flex-start;
      align-items: center;
}

.blue {
      background-color: blue;
}

.red {
      background-color: red;
}

.green {
      background-color: green;
}

.yellow {
      background-color: yellow;
}

.pink{
      color: #db346c;
}

.overflow{
      overflow: auto;
      scrollbar-width: none;  /* Firefox */
}

.overflow::-webkit-scrollbar {
      display: none;
}

